import React from 'react';
import {Tree} from "antd"
class MyTree extends React.Component {
    state={
        dck:[],
        arr:[],
       
    }
    derAuth(arr,narr){
       
        arr.map(v=>{
            let obj={}
            obj.title=v.authName
            obj.key=v.id
            obj.pid=v.pid
            if(v.children){
                obj.children=[]
                narr.push(obj)
                this.derAuth(v.children,obj.children)
            }else{
                narr.push(obj)
            }
        })

        return narr
    }
    componentDidMount(){
        this.setState({
            dck:this.props.dck,
            dek:this.props.dek
        })
    }
    render() { 
        let {dek,data,onCheck}=this.props
        let list=this.derAuth(data,[])
        return <div>
            <Tree           
            checkable={true}
            selectable={true}
            multiple={true}
            checkedKeys={this.state.dck}
            selectedKeys={this.state.dck}
            defaultSelectedKeys={this.state.dck}
            defaultExpandedKeys={dek}
            treeData={list}
            onCheck={(v1,v2)=>{
           
            this.setState({
                dck:v1
            })
            
                onCheck([...v1,...v2.halfCheckedKeys])
            }}
            />
        </div>;
    }
}
 
export default MyTree;
