import React from 'react';
import {Button, Table,Drawer,Space,Modal,Form,Input} from "antd"
import MyTree from "../../components/MyTree/MyTree"
class Roles extends React.Component {
    state={
        jslb:[],// 角色列表
        qxlb:[],// 权限列表
        visible:0,// 是否展示抽屉
        dek:[],// 权限分类的id的集合
        dck:[],// 权限id的集合
        cutjs:{},// 当前展示权限的角色
        qxid:[],// 权限id 分类id集合
        isModalVisible:0,// 模态框是否显示
        setjs:[
            {
                title:"id",
                dataIndex:"id",
                key:"id",
            },
            {
                title:"角色",
                dataIndex:"roleName",
                key:"roleName",

            },
            {
                title:"描述",
                dataIndex:"roleDesc",
                key:"roleDesc",

            },
            {
                title:"操作",
                key:"操作",
                render:(v)=>(<Button onClick={this.dispatchAuth.bind(this,v)}>分配权限</Button>)
            }
        ]
    }
    getJs(){// 获取角色列表
        this.api.get("roles")
        .then(res=>{
            console.log(res);
            if(res.data.meta.status==200){
                this.setState({
                    jslb:res.data.data
                })
            }
        })
    }

    getAuth(){// 获取权限
         this.api.get("rights/tree")
         .then(res=>{
             console.log(res);
             if(res.data.meta.status==200){
                 this.setState({
                     qxlb:res.data.data
                 })
             }
         })
    }

    dispatchAuth(v){// 分配权限
       console.log(v);
       // 清空之前的权限
       this.state.dek=[]
       this.state.dck=[]

       this.setState({
           dek:this.state.dek,
           dck:this.state.dck
       })
       this.getId(v.children)
       this.getAuth()
       console.log(this.state.dek);
        this.setState({
            visible:1,
            cutjs:v// 要分配权限的角色
        })

    }
    getId(arr){// arr 权限数组
         arr.map((v,k)=>{
            // v.id// 分类id 权限id
             if(v.children){// 分类id
                this.state.dek.push(v.id)
                this.getId(v.children)
             }else{// 权限id
               this.state.dck.push(v.id)
             }
         })

         this.setState({
             dck:this.state.dck,
             dek:this.state.dek
         })
    }
    onClose(){// 关闭抽屉
        this.getJs()// 回到最初的状态
        this.setState({
            visible:0
        })

    }
    sure(){
        console.log(this.state.cutjs.id);

        console.log(this.state.qxid);
        if(this.state.qxid.length==0){// 判断是否选中权限 如果没有动权限 其他下面逻辑不走
            this.setState({
                visible:0// 让抽屉消失
            })
            return 
        }
        // return
        // 组合参数
        let roleId=this.state.cutjs.id
       
         let  rids=this.state.qxid.join(",")// 组合参数 [102,103]=> '102,103'
        

        // console.log(obj);
        // 请求数据
        let params = new URLSearchParams()
        params.append("rids",rids)
        this.api.post("roles/"+roleId+"/rights",params)
        .then(res=>{
            console.log(res);
            if(res.data.meta.status==200){
                this.getJs()
                // 清空参数

                this.setState({
                    visible:0,
                    qxid:[]
                })
            }
        })
        
    }
    onCheck(v){// 分配权限 选择权限
        console.log(v);
        this.setState({
            qxid:v
        })
    }
    handleCancel(){// 取消模态框
         this.setState({
             isModalVisible:0
         })
    }
    addJs(v){// 添加角色
          console.log(v);
          // 组合参数
          let params=new URLSearchParams()
          for(let k in v){
              params.append(k,v[k])
          }

          // 发送请求
          this.api.post("roles",params)
          .then(res=>{
              console.log(res);
              if(res.data.meta.status==201){
                  // 更新数据
                  this.getJs()
                  // 取消模态框
                  this.setState({
                      isModalVisible:0
                  })
              }
          })
    }
    componentDidMount(){
        this.getJs()
        this.getAuth()
    }

    render() { 
        let {jslb,setjs,visible,qxlb,dek,dck,isModalVisible}=this.state
        return <div>
            <div style={{margin:10}}>
                <Button onClick={()=>{
                    this.setState({
                        isModalVisible:1
                    })
                }} type="primary">添加角色</Button>
            </div>
          <div style={{
              height:"80vh",
              overflow:"auto"
          }}>
          <Table dataSource={jslb} columns={setjs} childrenColumnName="none" />
          </div>

           {/* 抽屉 */}
           <Drawer  
           width={500} 
           title="分配权限" 
           placement="left" 
           onClose={this.onClose.bind(this)} 
           visible={visible}
          
           >
                {
                    visible?
                    <MyTree 
                    data={qxlb} 
                    onCheck={this.onCheck.bind(this)}
                    dek={dek}
                    dck={dck}
                    />:
                 ''
                }

                <div>
                    <Space>
              <Button onClick={this.onClose.bind(this)}>取消</Button>
              <Button type="primary" onClick={this.sure.bind(this)}>
                确认
              </Button>
            </Space></div>
            </Drawer>


             {/* 模态框 */}

             <Modal title="添加角色" visible={isModalVisible} 
            // onOk={this.handleOk.bind(this)}
            // okText="确认"
            cancelText="取消" 
            footer={false}
            onCancel={this.handleCancel.bind(this)}>
               <Form
                    name="basic"
                    labelCol={{ span: 4 }}
                    wrapperCol={{ span: 20 }}
                    initialValues={{ remember: true }}

                    onFinish={this.addJs.bind(this)}
                   
                    autoComplete="off"
                  >
                    <Form.Item
                      label="角色名称"
                      name="roleName"
                      
                      rules={[{ required: true, message: '请输入角色名' }]}
                    >
                      <Input />
                    </Form.Item>
                    <Form.Item
                      label="角色描述"
                      name="roleDesc"
                      
                      rules={[{ required: true, message: '请输入角色描述' }]}
                    >
                      <Input />
                    </Form.Item>


                   
                    <Form.Item wrapperCol={{ offset: 2, span: 22 }}>
                    <Button type="primary" htmlType="submit" >
                        添加
                      </Button>
                      <Button type="" style={{marginLeft:20}} onClick={this.handleCancel.bind(this)}>
                        取消
                      </Button>
                    </Form.Item>
                  </Form>
            </Modal>
        </div>;
    }
}
 
export default Roles;
