import React from 'react';
import {Table} from "antd"
class Orders extends React.Component {
    state={
        orderlist:[],// 表单列表
        total:0,// 总条数
        setorder:[// 配置项
            {
                title:"id",
                dataIndex:"order_id",
                key:"order_id"
            },
            {
                title:"订单编号",
                dataIndex:"order_number",
                key:"order_number"
            },
            {
                title:"价格",
                dataIndex:"order_price",
                key:"order_price"
            },

        ]
    }

    getOrder(pagenum,pagesize){// 获取订单列表
        this.api.get("orders",{params:{pagenum,pagesize}})
        .then(res=>{
            console.log(res);
            if(res.data.meta.status==200){
                this.setState({
                    orderlist:res.data.data.goods,
                    total:res.data.data.total
                })
            }
        })
    }
    onChange=(v)=>{
        console.log(v);
        this.getOrder(v,5)
    }
    componentDidMount(){
        this.getOrder(1,5)

    }
    render() { 
        let {orderlist,setorder,total}=this.state
        return <div>
            <Table 
            dataSource={orderlist} 
            columns={setorder}
            pagination={{
                total,
                onChange:this.onChange
            }}
              />
        </div>;
    }
}
 
export default Orders;