// 引入路由插件
import {HashRouter as Router,Route,Switch} from "react-router-dom"
// 引入路由组件
import Login from "./pages/Login/Login";
import Main from "./pages/Main/Main";
function App() {
  return (
    <div className="App">
      {/* 路由页面 */}
      <Router>
        <Switch>
           <Route exact path="/" component={Login} />
           <Route path="/main" component={Main} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
