import React from 'react';
import "./index.css"
import { Layout, Menu, Row,Col,Tag,Dropdown,Avatar } from 'antd';
// 引入路由插件
import {Route,Redirect } from "react-router-dom"
// 引入路由组件
import Users from '../Users/Users';
import Roles from '../Roles/Roles';
import Reports from '../Reports/Reports';
import Rights from '../Rights/Rights';
import Goods from '../Goods/Goods';
import Categories from '../Categories/Categories';
import Params from '../Params/Params';
import Orders from '../Orders/Orders';
import {
    PicCenterOutlined,
    ShoppingCartOutlined,
    InsertRowLeftOutlined,
    BarChartOutlined,
    UserOutlined,
    UnorderedListOutlined,
    CarryOutOutlined,
    MobileOutlined,
    LoginOutlined
  } from '@ant-design/icons';
  
  const { Header, Content, Footer, Sider } = Layout;
  const { SubMenu } = Menu;
class Main extends React.Component {
    state={
        collapsed:0,// 左侧菜单是否展开
        menu:[],// 左侧菜单数据
        cutsel:"users",// 当前选中状态
        dtnav:[
            {authName:"用户列表",path:"users"}
        ],// 头部动态导航
        icon:[
            <UserOutlined />,
            <PicCenterOutlined />,
            <ShoppingCartOutlined />,
            <InsertRowLeftOutlined />,
            <BarChartOutlined />
        ]
    }
    // 收起侧边展开
     onCollapse=(v)=>{
        //  v=!v
      this.setState({
          collapsed:v
      })
    }
    // 获取菜单
    getMenu(){
        this.api.get("menus")
        .then(res=>{
            console.log(res);
            if(res.data.meta.status==200){
                this.setState({
                    menu:res.data.data
                })
            }
        })

    }
    // 切换路由
    tab(i){
        console.log(i);
        // 添加动态导航
           // 判断是否已经添加过
           let t=this.state.dtnav.some((v,k)=>v.path==i.path)
           console.log(t);
           if(!t){// 不存在数组内部
               this.state.dtnav.push(i)
               
           }
        // 跳路由
        this.props.history.push("/main/"+i.path)
        // 高亮显示
        this.setState({
            cutsel:i.path,
            dtnav:this.state.dtnav
        })
    }

    removeSelf(v,k,e){// 动态导航 移出自己 最后一个非传参数是事件对象
        // 阻止默认事件 
        // e.prent
        // console.log(e);
        e.preventDefault();
        //    console.log(v);

        // 判断是否只剩一个
        if(this.state.dtnav.length<=1){
             return
        }

      
        // 判断关闭的标签是否是当前路由 高亮的标签
        if(v.path==this.state.cutsel){// 是高亮状态

               // 判断是否是关闭最后一个标签
               if(k==this.state.dtnav.length-1){// 是最后一个跳前边
                this.props.history.push("/main/"+this.state.dtnav[k-1].path)// 前边路由
                
                this.setState({
                    cutsel:this.state.dtnav[k-1].path// 前边高亮
                })
               }else{// 不是最后一个 跳后边
                this.props.history.push("/main/"+this.state.dtnav[k+1].path)// 后边路由
                
                this.setState({
                    cutsel:this.state.dtnav[k+1].path// 后边高亮
                })
               }
              
                // 删除下标对应的元素
                this.state.dtnav.splice(k,1)
        }else{
            // 删除下标对应的元素
            this.state.dtnav.splice(k,1)
        }
        this.setState({// 更新数据视图
            dtnav:this.state.dtnav
        })
    }
    dttab(v){// 头部动态导航切换
        console.log(v);
        this.props.history.push("/main/"+v.path)
        this.setState({// 更新高亮
            cutsel:v.path
        })
    }
    logout=()=>{// 退出登录
        // 1.清空token
        // 2.清空用户信息
        // 3.跳到登录页面
        sessionStorage.removeItem("token")
        sessionStorage.removeItem("userinfo")
        this.props.history.push("/")
        // console.log(666);
    }
    componentDidMount(){
        // 刷新之后到users
        this.props.history.push("/main/users")
        this.getMenu()
    }
    // 渲染菜单
    renderMenu(){
        let {menu,icon,cutsel}=this.state
       return menu.map((v,k)=>{
            return <SubMenu key={k} icon={icon[k]} title={v.authName}>
                {
                    v.children.map((i,j)=>{
                        return  <Menu.Item 
                        style={{
                            background:cutsel==i.path?'#018fff':'',
                            color:cutsel==i.path?'white':''
                        }}
                        icon={<UnorderedListOutlined />} 
                        key={i.path}
                        onClick={this.tab.bind(this,i)}
                        >{i.authName}</Menu.Item>
                    })
                }
           
               
            </SubMenu>
        })
    }

    renderUser(){// 渲染用户信息
         let userinfo =JSON.parse(sessionStorage.getItem("uesrinfo"))
         console.log(userinfo);
        return <Menu>
        <Menu.Item key="0">
        <UserOutlined /> {userinfo.username}
        </Menu.Item>
        <Menu.Item key="1">
        <CarryOutOutlined />{userinfo.email}
        </Menu.Item>
        <Menu.Item key="2">
        <MobileOutlined />{userinfo.mobile}
        </Menu.Item>
        <Menu.Divider />
        <Menu.Item key="3" onClick={this.logout}>
        <LoginOutlined /> 退出
        </Menu.Item>
      </Menu>

    }
    render() { 
        let {collapsed,dtnav}=this.state
        return <div>
          <Layout style={{ minHeight: '100vh' }}>
                <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse}>
                <div className="logo">
                    <img src="data:image/png;base64,"/>
                </div>
                <Menu 
                theme="dark" 
                defaultOpenKeys={["0"]} 
                // defaultSelectedKeys={['3']} 
                selectable={false}
                mode="inline">
                   
                    {this.renderMenu()}
                </Menu>
                </Sider>
                <Layout className="site-layout">
                <Header className="site-layout-background" style={{ padding: 0 }} >
                    <Row style={{marginLeft:"10px"}}>
                        <Col span={20}>
                            {
                               dtnav.map((v,k)=>{
                                   return <Tag 
                                   style={{cursor:"pointer"}}
                                   onClick={this.dttab.bind(this,v)}
                                   closable 
                                   color={v.path==this.state.cutsel?'#018fff':''}
                                   onClose={this.removeSelf.bind(this,v,k)}>
                                   {v.authName}
                                 </Tag>
                               })
                            }
                        </Col>
                        <Col span={4} >
                        <Dropdown 
                        overlay={this.renderUser()} 
                        trigger={['click']}
                        placement="bottomCenter"
                        >
                            <a style={{
                            height:"64px",
                            display:"flex",
                            alignItems:"center"
                        }} className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                            <div style={{
                                width:"50px",
                                height:"50px",
                                display:"flex",
                                justifyContent:"center",
                                alignItems:"center",
                                borderRadius:"50%",
                                boxShadow:"1px 2px 12px grey"
                            }}>
                                <Avatar style={{width:"100%",height:"100%"}} src="https://joeschmoe.io/api/v1/random" />
                            </div>
                            </a>
                        </Dropdown>
                        </Col>
                    </Row>
                </Header>
                <Content style={{ margin: '16px',background:'white' }}>
                    {/* 路由 */}
                    {/* 头开始展示 */}
                     <Route exact path="/main" render={()=><Redirect to="/main/users"/>} />
                     <Route  path="/main/users" component={Users} />
                     <Route  path="/main/roles" component={Roles} />
                     <Route  path="/main/rights" component={Rights} />
                     <Route  path="/main/goods" component={Goods} />
                     <Route  path="/main/categories" component={Categories} />
                     <Route  path="/main/orders" component={Orders} />
                     <Route  path="/main/params" component={Params} />
                     <Route  path="/main/reports" component={Reports} />
                </Content>
                </Layout>
            </Layout>
        </div>;
    }
}
 
export default Main;