import React from 'react';
import * as echarts from "echarts"//
import m from "lodash"
class Reports extends React.Component {
    getData(){
        this.api.get("reports/type/1")
        .then(res=>{
            console.log(res);
            this.renderEc(res.data.data)
        })
    }


    renderEc(data){
        let opt ={
            title: {
              text: '用户来源'
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                label: {
                  backgroundColor: '#E9EEF3'
                }
              }
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            xAxis: [
              {
                boundaryGap: false
              }
            ],
            yAxis: [
              {
                type: 'value'
              }
            ]
          }
       let myEcharts=echarts.init(document.getElementById('main'))
       let res=m.merge(data,opt)
       myEcharts.setOption(res)
    }
    componentDidMount(){
        this.getData();
    }
    render() { 
        return <div>
            <div style={{
                width:1200,
                height:700
            }} id="main"></div>
        </div>;
    }
}
 
export default Reports;