import React from 'react';
import "./index.css"
import { Table, Switch, Button, message, Dropdown, Menu, Modal, Form, Input } from "antd"
class Users extends React.Component {
    state = {
        user: [],// 用户列表
        jslb: [],// 角色列表
        isModalVisible: 0,// 模态框是否显示
        total: 0,// 数据总数
        setuser: [// 用户列表配置项
            {
                title: 'id',
                dataIndex: 'id',
                key: 'id',
            },
            {
                title: '用户',
                dataIndex: 'username',
                key: 'username',
            },
            {
                title: '角色',
                dataIndex: 'role_name',
                key: 'role_name',
            },
            {
                title: '邮箱',
                dataIndex: 'email',
                key: 'email',
            },
            {
                title: "权限",
                // dataIndex:"mg_state",
                key: "mg_state",
                render: (v) => (<Switch onChange={this.upState.bind(this, v)} checked={v.mg_state} />)


            },
            {
                title: "操作",
                key: "操作",
                render: (v) => (<Dropdown
                    overlay={this.renderJs(v.id)} /**传用户id */
                    trigger={['click']}
                    placement="bottomCenter"

                >
                    <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                        <Button >分配角色</Button>
                    </a>
                </Dropdown>)

            },
            {
                title: "删除",
                key: "删除",
                render: (v) => (<Button type="danger" onClick={this.delUser.bind(this, v.id)}>删除</Button>)
            }

        ]
    }
    getUsers(params) {//  获取用户列表
        this.api.get("users", { params })
            .then(res => {
                console.log(res);
                if (res.data.meta.status == 200) {
                    this.setState({
                        user: res.data.data.users,
                        total: res.data.data.total
                    })
                }
            })
    }
    getJs() {// 获取角色列表
        this.api.get("roles")
            .then(res => {
                console.log(res);
                if (res.data.meta.status == 200) {
                    this.setState({
                        jslb: res.data.data
                    })
                }
            })
    }

    fpjs(id, rid) {// 分配角色 id 用户id rid 角色id
        // console.log(id,rid);
        this.api.put("users/" + id + "/role", { rid })
            .then(res => {
                //    console.log(res);
                if (res.data.meta.status == 200) {
                    this.getUsers({ pagenum: 1, pagesize: 5 })
                    message.info('设置成功', 1);

                }
            })
    }
    delUser(id) {
        this.api.delete("users/" + id)
            .then(res => {
                console.log(res)
                if (res.data.meta.status == 200) {
                    message.success("删除成功", 1)
                    this.getUsers({ pagenum: 1, pagesize: 5 })
                }
            })
    }
    upState(v, v1) {// 修改状态  v 整条数据 v1 修改之后的状态
        console.log(v);
        console.log(v1);
        // 请求数据
        v.mg_state = v1
        this.setState({
            user: this.state.user
        })
        this.api.put("users/" + v.id + "/state/" + v1)
            .then(res => {
                console.log(res);
                if (res.data.meta.status == 200) {
                    // this.getUsers({pagenum:1,pagesize:5})
                    message.info('修改成功', 1);
                }
            })

    }
    handleOk() {
        this.setState({
            isModalVisible: 1
        })
    }
    handleCancel() {
        this.setState({
            isModalVisible: 0
        })
    }
    addUser(e) {
        console.log(e);
        let params = new URLSearchParams()
        for (let k in e) {
            params.append(k, e[k])
        }
        this.api.post("users", params)
            .then(res => {
                console.log(res);
                if (res.data.meta.status == 201) {
                    this.getUsers({ pagenum: 1, pagesize: 5 })// 更新数据
                    this.setState({
                        isModalVisible: 0
                    })
                    message.info("添加成功", 1)
                }
            })
    }
    fy(page, pagesize) {// 分页
        this.getUsers({ pagenum: page, pagesize })
    }
    componentDidMount() {
        this.getUsers({ pagenum: 1, pagesize: 5 })
        this.getJs()
    }
    renderJs(id) {// id 用户id
        return <Menu>
            {
                this.state.jslb.map((v, k) => {
                    return <Menu.Item
                        onClick={this.fpjs.bind(this, id, v.id)}
                        key={v.roleName}>
                        {v.roleName}
                    </Menu.Item>
                })
            }

        </Menu>
    }
    render() {
        let { user, setuser, isModalVisible, total } = this.state
        return <div>
            <div style={{ margin: 10 }}>
                <Button onClick={this.handleOk.bind(this)} type="primary">添加用户</Button>
            </div>
            <Table
                dataSource={user}
                columns={setuser}
                pagination={{
                    position: ["bottomLeft"],
                    pageSize: 5,
                    total,
                    onChange: this.fy.bind(this)

                }}
            />

            {/* 模态框 */}

            <Modal title="添加用户" visible={isModalVisible}
                // onOk={this.handleOk.bind(this)}
                // okText="确认"
                cancelText="取消"
                footer={false}
                onCancel={this.handleCancel.bind(this)}>
                <Form
                    name="basic"
                    labelCol={{ span: 3 }}
                    wrapperCol={{ span: 21 }}
                    initialValues={{ remember: true }}

                    onFinish={this.addUser.bind(this)}

                    autoComplete="off"
                >
                    <Form.Item
                        label="用户"
                        name="username"

                        rules={[{ required: true, message: '请输入用户名' }]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        label="密码"
                        name="password"
                        rules={[{ required: true, message: '请输入密码' }]}
                    >
                        <Input.Password />
                    </Form.Item>
                    <Form.Item
                        label="邮箱"
                        name="email"

                        rules={[{ required: true, message: '请输入邮箱' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="手机"
                        name="mobile"

                        rules={[{ required: true, message: '请输入手机' }]}
                    >
                        <Input />
                    </Form.Item>


                    <Form.Item wrapperCol={{ offset: 2, span: 22 }}>
                        <Button type="primary" htmlType="submit" >
                            添加
                        </Button>
                        <Button type="" style={{ marginLeft: 20 }} onClick={this.handleCancel.bind(this)}>
                            取消
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>
        </div>;
    }
}

export default Users;