import React from 'react';
import { Table } from "antd"

class Goods extends React.Component {
    state = {
        goods: [],// 商品列表
        total: 0,// 商品总数
        setgoods: [// 商品列表配置项
            {
                title: "id",
                dataIndex: "goods_id",
                key: "goods_id"
            },
            {
                title: "名称",
                dataIndex: "goods_name",
                key: "goods_name",

            },
            {
                title: "价格",
                dataIndex: "goods_price",
                key: "goods_price",
                render: (v) => (<span>￥{v}</span>)
            },
        ],
    }
    getGoods(params) {// 获取商品列表
        this.api.get("goods", { params })
            .then(res => {
                console.log(res);
                if (res.data.meta.status == 200) {
                    this.setState({
                        goods: res.data.data.goods,
                        total: res.data.data.total
                    })
                }
            })
    }
    fy(page, pagesize) {// 分页
        this.getGoods({ pagenum: page, pagesize })
    }

    componentDidMount() {
        this.getGoods({ pagenum: 1, pagesize: 10 })
    }
    render() {
        let { goods, setgoods, total } = this.state
        return <div>
            <Table
                dataSource={goods}
                columns={setgoods}
                pagination={{
                    position: ["bottomLeft"],
                    pageSize: 10,
                    total,
                    onChange: this.fy.bind(this)
                }}
            />
        </div>;
    }
}

export default Goods;