import React from 'react';
import bg from "../../assets/bg.webp"
import { Card,Form, Input, Button } from 'antd';
class Login extends React.Component {
    state={

    }
    // 登录
    Login(v){
        // 组合参数
        let params=new URLSearchParams()
        params.append("username",v.username)
        params.append("password",v.password)
        // console.log(v);
        // 方式请求
        this.api.post("login",params)
        .then(res=>{
            // console.log(res);
            // 判断登录成功
            if(res.data.meta.status==200){
              // token存session
              sessionStorage.setItem("token",res.data.data.token) 
              // 存用户信息
              let {email,username,mobile}=res.data.data// 解构赋值
              let obj={email,username,mobile}// 组合用户信息
              sessionStorage.setItem("uesrinfo",JSON.stringify(obj))
              // 跳到main
              this.props.history.replace("/main")
              // console.log(obj);
            }
        })
    }
    componentDidMount(){

    }
    render() { 
        return <div style={{
            position:"fixed",
            top:"0",
            left:"0",
            width:"100%",
            height:"100%"
        }}>
           {/* <button onClick={this.Login.bind(this)}>登录</button> */}
           <img width="100%" height="100%" src={bg}/>
           <div style={{
               position:"absolute",
               top:"300px",
               left:"50%",
               transform:"translateX(-50%)",
           }}>
               <Card title="登录"  style={{ width: 500,height:300 }}>
               <Form
                    name="basic"
                    labelCol={{ span: 3 }}
                    wrapperCol={{ span: 21 }}
                    initialValues={{ remember: true }}
                    onFinish={this.Login.bind(this)}
                   
                    autoComplete="off"
                  >
                    <Form.Item
                      label="用户"
                      name="username"
                      initialValue="admin"
                      rules={[{ required: true, message: '请输入用户名' }]}
                    >
                      <Input />
                    </Form.Item>

                    <Form.Item
                      label="密码"
                      name="password"
                      initialValue="123456"
                      rules={[{ required: true, message: '请输入密码' }]}
                    >
                      <Input.Password />
                    </Form.Item>

                   
                    <Form.Item wrapperCol={{ offset: 2, span: 22 }}>
                      <Button type="primary" htmlType="submit" style={{width:"100%"}}>
                        登录
                      </Button>
                    </Form.Item>
                  </Form>
               </Card>
           </div>
        </div>;
    }
}
 
export default Login;