import React from 'react';
import {Table,Tag} from "antd"
class Categories extends React.Component {
    state={
        cate:[],// 分类列表 树
        setcate:[// 分类列表 配置项
            {
                title:"id",
                dataIndex:"cat_id",
                key:"cat_id"
            },
            {
                title:"分类名称",
                // dataIndex:"cat_name",
                key:"cat_name",
                render:(v)=>{
                    let arr=[
                        {name:"一级分类",color:'pink'},
                        {name:"二级分类",color:"skyblue"},
                        {name:"三级分类",color:"yellow"}
                    ]
                    return<Tag 
                        color={arr[v.cat_level].color}
                    >
                        {v.cat_name}
                    </Tag>
                }
            },
            {
                title:"分类级别",
                dataIndex:"cat_level",
                key:"cat_level",
                render:(v)=>{
                    let arr=[
                        {name:"一级分类",color:'pink'},
                        {name:"二级分类",color:"skyblue"},
                        {name:"三级分类",color:"yellow"}
                    ]
                    return <Tag color={arr[v].color}>
                        {
                           arr[v].name
                        }
                    </Tag>
                }
            },
        ]
    }
    getCate(){// 获取分类列表
        this.api.get("categories")
        .then(res=>{
            if(res.data.meta.status==200){
                this.setState({
                    cate:res.data.data
                })
            }
        })
    }
    componentDidMount(){
        this.getCate()
    }
    render() { 
        let {cate,setcate}=this.state
        return <div>
            <Table dataSource={cate} rowKey={(v)=>v.cat_id} columns={setcate} />
        </div>;
    }
}
export default Categories;