import React from 'react';
import {Table,Tag,Tabs}  from "antd"
const { TabPane } = Tabs;
class Rights extends React.Component {
    state={
        qxlist:[],// 权限列表
        qxtree:[],// 权限树
        ecarr:[],// 要展开的行
        setlist:[// 权限列表配置项
            {
               title:"id",
               dataIndex:"id",
               key:"id"
            },
            {
                title:"权限说明",
                dataIndex:"authName",
                key:"authName"

            },
            {
                title:"权限等级",
                dataIndex:"level",
                key:"level",
                render:(v)=>{
                    let arr=[
                        {name:"一级权限",color:'pink'},
                        {name:"二级权限",color:"skyblue"},
                        {name:"三级权限",color:"yellow"}
                    ]
                    return <Tag color={arr[v].color}>
                        {
                           arr[v].name
                        }
                    </Tag>
                }
            },
            {
                title:"权限作用",
                dataIndex:"path",
                key:"path"
            }
        ],
        settree:[// 树配置
              {
                  title:"id",
                  dataIndex:"id",
                  key:"id"
              },
              {
                  title:"权限说明",
                  dataIndex:"authName",
                  key:"authName"
              },
              {
                  title:"权限作用",
                  dataIndex:"path",
                  key:'path'
              }
        ]
    }
    getAuthList(){// 获取权限列表
        this.api.get("rights/list")
        .then(res=>{
            if(res.data.meta.status==200){
                this.setState({
                    qxlist:res.data.data
                })
            }
        })
    }
    getAuthTree(){
        this.api.get("rights/tree")
        .then(res=>{
            console.log(res);
            if(res.data.meta.status==200){
                this.setState({
                    qxtree:res.data.data
                })
            }
        })
    }
    // onExpand(b,v){// 展开
    //     let {ecarr}=this.state
    //   console.log(b);
    //   console.log(v);
      
    // }

    componentDidMount(){
       this.getAuthList()
       this.getAuthTree()
    }
    
    render() { 
        let {qxlist,setlist,qxtree,settree,ecarr}=this.state
        return <div style={{
            boxSizing:"border-box",
            padding:10
        }}>
             <Tabs defaultActiveKey="1" >
                <TabPane tab="列表展示" key="1">
                <Table dataSource={qxlist} columns={setlist}/>
                </TabPane>
                <TabPane tab="树展示" key="2">
                <Table 
                dataSource={qxtree} 
                columns={settree}
                rowKey={(v)=>v.id}
                // expandedRowKeys={ecarr}
                expandRowByClick={true}
                rowExpandable={()=>true}
                pagination={false}
                // onExpand={this.onExpand.bind(this)}
                />
                </TabPane>
                
            </Tabs>
            
        </div>;
    }
}
 
export default Rights;