import React from 'react';
import {Cascader,Divider,Tabs,Table, Button,Modal,Form,Input,message} from "antd"
const { TabPane } =Tabs

let cutsel='only'// 全局变量控制 动静态参数
class Params extends React.Component {
    
    state={
        cate:[],// 分类列表
        sel:"only",// 静态动态参数
        cid:null,// 分类id
        onlyarr:[],// 静态参数
        manyarr:[],// 动态参数
        isModalVisible:0,// 模态框展示
        setonly:[// 静态参数配置项
            {
                title:"id",
                dataIndex:"attr_id",
                key:"attr_id"
            },
            {
                title:"分类名称",
                dataIndex:"attr_name",
                key:"attr_name"
            },
            {
                title:"操作",
                key:"操作",
                render:(v)=>(<Button type="danger" onClick={this.delParams.bind(this,v.attr_id)}>删除</Button>)
            }
        ],
        setmany:[// 动态参数配置项
            {
                title:"id",
                dataIndex:"attr_id",
                key:"attr_id"
            },
            {
                title:"分类名称",
                dataIndex:"attr_name",
                key:"attr_name"
            },
            {
                title:"操作",
                key:"操作",
                render:(v)=>(<Button type="danger" onClick={this.delParams.bind(this,v.attr_id)}>删除</Button>)
            }
        ]
    }
    getCate(){// 获取分类列表
        this.api.get("categories")
        .then(res=>{
            if(res.data.meta.status==200){
                this.setState({
                    cate:res.data.data
                })
            }
        })
    }
    getParams(v){
        let {sel}=this.state
        this.api.get("categories/"+v+"/attributes",{params:{sel:cutsel}})// 使用全局变量
        .then(res=>{
            console.log(res);
            if(res.data.meta.status==200){
                this.setState({
                    onlyarr:cutsel=="only"?res.data.data:[],
                    manyarr:cutsel=="many"?res.data.data:[]
                })
            }
        })
    }
    select(v){/// 选择分类
      console.log(v[2]); 
        
      if(!v[2]){
          return
      }

      // 存当前分类id
      this.setState({
          cid:v[2]
      })
      this.getParams(v[2])

    }
    tab(v){// 切换动态静态参数
        console.log(v);// key 
        cutsel=v// 给全局变量赋值
        // this.setState({
        //     sel:v// 更新的是上一次的状态
        // })
        // console.log(this.state.sel);// v 同步
        console.log(cutsel);
        // return
        this.getParams(this.state.cid)
    }

    addParams(v){// 添加参数 召唤模态框
      console.log(v);
    //   this.refs.form.resetFields()

      // 判断是不是选择了某个分类
      console.log(this.state.cid);
      if(!this.state.cid){// 没有选择分类
        message.info("请选择正确的分类")
           return 
      }
       this.state.sel=v
       this.setState({
           isModalVisible:1,
           sel:this.state.sel

       })
    //    console.log(this.state.sel);

    }
    delParams(id){
        console.log(id);
        console.log(this.state.cid);
        console.log(cutsel);
        // return 
        this.api.delete("categories/"+this.state.cid+"/attributes/"+id)
        .then(res=>{
            console.log(res);
            if(res.data.meta.status==200){
                message.success("删除成功")
                this.getParams(this.state.cid)
            }
        })
    }
    handleCancel(){// 关闭模态框
        console.log(this.refs.form);
        this.refs.form.resetFields()// 清空表单
        this.setState({
            isModalVisible:0
        })
    }
    sureAdd(v){// 确认添加参数
        // 组合参数
        let obj={
           
            attr_sel:this.state.sel,
            ...v
        }
        console.log(obj);
        // 处理参数
        let param=new URLSearchParams()
        for(let k in obj){
            param.append(k,obj[k])
        }
        // 发送请求
        // return 
        this.api.post("categories/"+this.state.cid+"/attributes",param)
        .then(res=>{
            console.log(res);
            if(res.data.meta.status==201){
                this.setState({
                    isModalVisible:0
                })
                cutsel=this.state.sel
                this.getParams(this.state.cid)
                this.refs.form.resetFields()// 清空表单
            }
        })
    }
    onFinish(v){
        console.log(v)
        // console.log(this.props.form);
        // this.refs.form.resetFields()
        // v={}
        // form.resetFields();
        // this.refs.form={}
    }
    componentDidMount(){
        this.getCate()
    }
    render() { 
        let {cate,onlyarr,setonly,setmany,manyarr,isModalVisible}=this.state
        return <div style={{
            boxSizing:"border-box",
            padding:"10px"
        }}>
            <Cascader
                // defaultValue={['zhejiang', 'hangzhou', 'xihu']}
                options={cate}
                fieldNames={{
                    value:"cat_id",
                    label:"cat_name",
                    children:"children"
                }}
                onChange={this.select.bind(this)}
            />

           <Divider plain>展示分类参数</Divider>
           {/* <Button type="primary" onClick={this.addParams.bind(this,'only')}>添加静态参数</Button>
           <Button type="primary" onClick={this.addParams.bind(this,'many')}>添加动态参数</Button> */}
           
           <Tabs defaultActiveKey="only" onChange={this.tab.bind(this)}>
                <TabPane tab="静态参数" key="only">
                 <Button type="primary" onClick={this.addParams.bind(this,'only')}>添加静态参数</Button>
                  <Table dataSource={onlyarr} columns={setonly} />
                </TabPane>
                <TabPane tab="动态参数" key="many">
                 <Button type="primary" onClick={this.addParams.bind(this,'many')}>添加动态参数</Button>

                <Table dataSource={manyarr} columns={setmany} />
                </TabPane>
               
            </Tabs>


            <Modal title="添加参数" visible={isModalVisible} 
            // onOk={this.handleOk.bind(this)}
            // okText="确认"
            cancelText="取消" 
            footer={false}
            onCancel={this.handleCancel.bind(this)}
            >
             
                   <Form
                    onFinish={this.onFinish.bind(this)}
                    name="basic"
                    labelCol={{ span: 3 }}
                    wrapperCol={{ span: 21 }}
                    initialValues={{ remember: true }}

                    onFinish={this.sureAdd.bind(this)}
                    ref="form"
                    autoComplete="off"
                  >
                    <Form.Item
                      label="参数名"
                      name="attr_name"
                      
                      rules={[{ required: true, message: '请输入参数名' }]}
                    >
                      <Input />
                    </Form.Item>

                    
                    <Form.Item
                      label="参数值"
                      name="attr_vals"
                    
                      rules={[{ required: true, message: '请输入参数值' }]}
                    >
                      <Input />
                    </Form.Item>
                   

                   
                    <Form.Item wrapperCol={{ offset: 2, span: 22 }}>
                    <Button type="primary" htmlType="submit" >
                        添加
                      </Button>
                      <Button type="" style={{marginLeft:20}} onClick={this.handleCancel.bind(this)}>
                        取消
                      </Button>
                    </Form.Item>
                  </Form>
              
            </Modal>
        </div>;
    }
}
 
export default Params;